import "./../btnNetlify/btnNetlify.css";
import netlifyIcon from "./netlify.svg";

const BtnNetlify = ({ link }) => {
	return (
		<a href={link} target="_blank" rel="noreferrer" className="btn-outline">
			<img src={netlifyIcon} alt="Netlify" />
			Netlify
		</a>
	)
}

export default BtnNetlify;