import "./../footer/footer.css";
import vk from "./../../img/icons/vk.svg";
import gitHub from "./../../img/icons/gitHub.svg";
import wa from "./../../img/icons/wa.svg";
import telegram from "./../../img/icons/telegram.svg";


const Footer = () => {
	return (
		<footer className="footer">
			<div className="container">
				<div className="footer__wrapper">
					<ul className="social">
						<li className="social__item"><a href="https://github.com/f4soul" target="_blank" rel="noreferrer"><img src={gitHub} alt="Link" /></a></li>
						<li className="social__item"><a href="https://vk.com/f4soul" target="_blank" rel="noreferrer"><img src={vk} alt="Link" /></a></li>
						<li className="social__item"><a href="https://t.me/+79152364602" target="_blank" rel="noreferrer"><img src={telegram} alt="Link" /></a></li>
						<li className="social__item"><a href="https://wa.me/79152364602" target="_blank" rel="noreferrer"><img src={wa} alt="Link" /></a></li>
					</ul>
					<div className="copyright">
						<p>© 2023 f4soul</p>
					</div>
				</div>
			</div>
		</footer>
	)
}

export default Footer;